import { PluginComponentType, registerComponent } from "@fiftyone/plugins";
import { Button } from "@fiftyone/components";
import styled from "styled-components";

// Styled Container
const Container = styled.div`
    margin: 1em;
    display: flex;
    flex-direction: column;
    align-items: center;
    row-gap: 1em;
`;

// OHIF Viewer Panel Component
export function OHIFViewerPanel() {
    const openOHIFViewer = () => {
        // Redirect to the OHIF Viewer in the same tab
        window.location.href = "https://viewer.ohif.org/";

        // OR open in a new tab
        // window.open("https://viewer.ohif.org/", "_blank");
    };

    return (
        <Container>
            <h3>OHIF Viewer Panel</h3>
            <Button onClick={openOHIFViewer} style={{ width: "200px" }}>
                Open OHIF Viewer
            </Button>
        </Container>
    );
}

// Register the Component
registerComponent({
    name: "OHIFViewerPanel",
    label: "OHIF Viewer Panel",
    component: OHIFViewerPanel,
    type: PluginComponentType.Panel,
});
